:root {
    --bs-border-color: var(--tc-system-stroke) !important;
}

// color-homepage
body {
    &.crypto-agency {
        .mobile-header-wrapper-style {
            background-color: #040826;
        }

        .mobile-header-wrapper-style .mobile-header-wrapper-inner .mobile-header-content-area .mobile-menu-wrap nav .mobile-menu li a {
            color: #fff;
        }

        background-color: #040826;
        --tc-theme-primary: #50eefa;
        --bs-primary-rgb: 80, 238, 250;
        --bs-border-radius-xl: 10px !important;
        --bs-border-radius-xxl: 20px !important;

        .bg-dark-2 {
            background-color: #03061c;
        }

        .text-secondary-2 {
            color: var(--tc-black-bg-body-text);
        }

        .text-linear {
            background: var(--Liner, linear-gradient(180deg, #fff 0%, rgba(26, 28, 76, 0) 100%));
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
}

// background colors
.bg-primary {
    background-color: var(--tc-theme-primary) !important;
}

.bg-dark-2 {
    background: var(--tc-system-black-2);
}

// text colors
.text-primary {
    color: var(--tc-theme-primary) !important;
}

.text-dark {
    color: var(--tc-system-black) !important;
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

// variables end
.border-linear {
    background:
        linear-gradient(#060b2e, #060b2e) padding-box,
        linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(26, 28, 76, 0)) border-box;
    border: 2px solid transparent;
}

.border-linear-primary {
    background:
        linear-gradient(#03061c, #03061c) padding-box,
        linear-gradient(to bottom, rgba(177, 227, 70, 0.3), rgba(26, 28, 76, 0)) border-box;
    border: 1px solid transparent;
}
.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
